/* eslint-disable max-lines */
import React from 'react';
import PropTypes from 'prop-types';
import ReactModal from 'react-modal';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import { MODAL_TYPE_V2, MODAL_SUB_TYPE_V2 } from './components/types';
import { DSModalContent } from './components/ModalContent';

const DSModal = ({
  containerProps = {},
  className = '',
  style = {},
  size = 'default',
  //
  modalType = MODAL_TYPE_V2.INFORMATION,
  modalSubType = null,
  modalTitle = '',
  centered = false,
  showRejectButton = false,
  showClose = true,
  searchProps = {},
  // override
  actionsRef = () => null,
  additionalFooterCssClass,
  overridePropsConfirmButton = {},
  overridePropsRejectButton = {},
  // react modal
  children,
  isOpen = true,
  onClose = () => null,
  onAfterOpen = () => null,
  onConfirm = () => null,
  onReject = () => null,
  rejectLabel = 'Discard',
  confirmLabel = 'Save',
  shouldCloseOnOverlayClick = false,
  appElement = '#root',
  zIndex = 10,
  removePadding = false,
}) => {
  if (
    typeof document !== 'undefined' &&
    process.env.NODE_ENV !== 'test' &&
    document &&
    ReactModal.setAppElement
  ) {
    ReactModal.setAppElement(appElement);
  }

  const {
    cssClassName,
    classNameBlock,
    classNameModifier,
  } = convertPropToCssClassName('modal-v2', className, { size });

  return (
    <ReactModal
      className={`${cssClassName} ${classNameBlock(`type-${modalType}`)}`}
      contentLabel={modalTitle}
      isOpen={isOpen}
      onAfterOpen={onAfterOpen}
      onRequestClose={onClose}
      overlayClassName={`${classNameBlock('overlay')} ${classNameModifier(
        centered ? 'center' : 'top',
      )}`}
      shouldCloseOnOverlayClick={shouldCloseOnOverlayClick}
      style={{
        content: {
          ...style,
        },
        overlay: {
          // position: 'absolute', https://jira.elliemae.io/browse/PUI-1471, the overlay should be positioned based on the browser window rather positioned absolute to its parent
          zIndex,
        },
      }}
    >
      <DSModalContent
        actionsRef={actionsRef}
        additionalFooterCssClass={additionalFooterCssClass}
        confirmLabel={confirmLabel}
        containerProps={containerProps}
        modalSubType={modalSubType}
        modalTitle={modalTitle}
        modalType={modalType}
        onClose={onClose}
        onConfirm={onConfirm}
        onReject={onReject}
        overridePropsConfirmButton={overridePropsConfirmButton}
        overridePropsRejectButton={overridePropsRejectButton}
        rejectLabel={rejectLabel}
        searchProps={searchProps}
        showClose={showClose}
        showRejectButton={showRejectButton}
        removePadding={removePadding}
      >
        {children}
      </DSModalContent>
    </ReactModal>
  );
};

DSModal.propTypes = {
  containerProps: PropTypes.object,
  className: PropTypes.string,
  style: PropTypes.object,
  modalTitle: PropTypes.string,
  centered: PropTypes.bool,
  showRejectButton: PropTypes.bool,
  showClose: PropTypes.bool,
  /*
    Props for search component
  */
  searchProps: PropTypes.object,
  additionalFooterCssClass: PropTypes.string,
  overridePropsConfirmButton: PropTypes.object,
  overridePropsRejectButton: PropTypes.object,
  isOpen: PropTypes.bool,
  onClose: PropTypes.func,
  onAfterOpen: PropTypes.func,
  onConfirm: PropTypes.func,
  onReject: PropTypes.func,
  rejectLabel: PropTypes.string,
  confirmLabel: PropTypes.string,
  size: PropTypes.oneOf([
    'default',
    'xx-large',
    'x-large',
    'large',
    'medium',
    'small',
  ]),
  modalSubType: PropTypes.any,
  modalType: PropTypes.any,
  actionsRef: PropTypes.any,
  children: PropTypes.element,
  shouldCloseOnOverlayClick: PropTypes.bool,
  /*
    App element ID to inject the modal
  */
  appElement: PropTypes.string,
  zIndex: PropTypes.number,
  removePadding: PropTypes.bool,
};

export { MODAL_TYPE_V2, MODAL_SUB_TYPE_V2, DSModalContent };
export default DSModal;
